import "../chord.css";
import { ChordList, ChordType } from "../ChordList";
type replaceType = { org: string; dst: string };

export const Chord = ({
  //‰∏ÄËá¥„Ç≥„Éº„ÉâÊ§úÁ¥¢
  chordName,
  clef,
}: {
  chordName: string;
  clef: string;
}) => {
  let chordExists = false;
  let matchChord: ChordType = {
    name: "",
    notes: 3,
    lineG: { position: [] },
    lineF: { position: [] },
    accidental: { kind: [] },
  };

  for (const element of ChordList) {
    if (chordName === element.name) chordExists = true;
    if (element.name2) {
      if (chordName === element.name2) chordExists = true;
    }
    if (chordExists) {
      Object.assign(matchChord, element);
      break;
    }
  }

  //ÂÖ•Âäõ„Åï„Çå„Åü„Ç≥„Éº„Éâ„ÅåÂ≠òÂú®„Åó„Å¶„ÅÑ„Å™„ÅÑ„Å™„ÇâÁÑ°„ÇíËøî„Åô
  if (!chordExists) return <div></div>;
  //Â≠òÂú®„Åó„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØdivÂÜÖÂÆπ„Çí‰ΩúÊàê„Åô„ÇãÈñ¢Êï∞„Å´„Ç≥„Éº„ÉâÊÉÖÂ†±„ÇíÊ∏°„Åô
  return createNoteDivContent({ matchChord, clef });
};

const createNoteDivContent = ({
  matchChord,
  clef,
}: {
  matchChord: ChordType;
  clef: string;
}) => {
  let lineFlg = false;
  let lineArray: boolean[] = [];
  let lineArraySize = 0;
  let accidentalArraySize = 0;
  let chordClef = "g";
  let cssClassChordName = "";
  let replaceClassName: replaceType[] = [
    //„ÇØ„É©„ÇπÂêçÁΩÆÊèõÁî®
    { org: "‚ô≠", dst: "f" },
    { org: "‚ôØ", dst: "s" },
  ];
  let noteArray = []; //Âá∫ÂäõdivÈÖçÂàó

  //„Ç≥„Éº„ÉâÂêç„Å´‚ô≠„Å®‚ôØ„Åå„ÅÇ„ÇãÂ†¥ÂêàCSSÁî®„Å´'f'„Å®'s'„Å´ÁΩÆÊèõ
  cssClassChordName = matchChord.name;
  for (const element of replaceClassName) {
    if (matchChord.name.match(element.org)) {
      cssClassChordName = matchChord.name.replace(
        new RegExp(element.org + "(.*?)", "g"),
        element.dst
      );
      break;
    }
  }

  //ËøΩÂä†„ÅÆÁ∑ö„ÉàÈü≥„Å∏Èü≥„Å©„Å°„Çâ„Çí‰ΩøÁî®„Åô„Çã„ÅãË®≠ÂÆö
  if (clef === "ùÑû") {
    if (matchChord.lineG) {
      lineFlg = true;
      lineArray = matchChord.lineG.position;
    }
  } else {
    chordClef = "f";
    if (matchChord.lineF) {
      lineFlg = true;
      lineArray = matchChord.lineF.position;
    }
  }

  //ÊØéÂõû.length„Åó„Å™„ÅÑÁî®
  if (typeof matchChord.accidental !== "undefined")
    accidentalArraySize = matchChord.accidental.kind.length;
  if (lineArray) lineArraySize = lineArray.length;

  //Ë°®Á§∫È†Ü(Á∑ö->Ë®òÂè∑->Èü≥Á¨¶)„ÅßdivËøΩÂä†
  for (let i = 1; i < matchChord.notes + 1; i++) {
    //Á∑ö
    if (lineFlg) {
      //ÈÖçÂàó„ÅåÂ≠òÂú®+i„ÅÆ‰ΩçÁΩÆ„Ååtrue
      if (0 < lineArraySize && lineArray[i - 1] === true) {
        //„Å∏Èü≥„Åß5Êú¨Á∑ö„Çà„Çä‰∏ä„Å´Á∑ö„ÇíËøΩÂä†„Åô„ÇãÂ†¥Âêà„ÄÅ‰ΩçÁΩÆË™øÊï¥„ÅåÂøÖË¶Å„Å™„ÅÆ„ÅßCSS„ÅÆ„ÇØ„É©„ÇπÂêç„ÇíÂà•„Å´Ë®≠ÂÆö„Åô„Çã
        if (1 < i && clef === "ùÑ¢") chordClef = "f";
        let lineName =
          "line " +
          cssClassChordName +
          "-" +
          chordClef +
          "-" +
          String(i) +
          "-l";
        noteArray.push(<div className={lineName}>-</div>);
        //ÊúÄÂàù„ÅÆË¶ÅÁ¥†‰ª•Â§ñ„ÉàÈü≥„Å∏Èü≥„ÅÆÈü≥„ÇÑË®òÂè∑„ÅÆÁõ∏ÂØæ‰ΩçÁΩÆ„ÅØÂü∫Êú¨ÁöÑ„Å´Âêå„Åò
        if (i === 1) chordClef = "g";
      }
    }

    //„ÉàÈü≥„ÅÆ1Áï™ÁõÆ„Åæ„Åü„ÅØ2Áï™ÁõÆ„Å´Á∑ö„ÅåÊù•„ÇãÂ†¥Âêà„Å∏Èü≥„ÅÆ‰ΩçÁΩÆË®≠ÂÆö„ÇíÂà•„Å´Ë°å„ÅÜÂøÖË¶Å„Åå„ÅÇ„Çã(root„ÅåB„Åæ„Åü„ÅØB‚ô≠„ÅÆÂ†¥Âêà)
    if (matchChord.lineG) {
      if (
        (matchChord.lineG.position[1] === true ||
          matchChord.lineG.position[2] === true) &&
        clef === "ùÑ¢"
      )
        chordClef = "f";
    }

    //Ë®òÂè∑
    if (
      accidentalArraySize !== 0 &&
      typeof matchChord.accidental !== "undefined"
    ) {
      //ÂèÇÁÖßÁÆáÊâÄ„ÅåÈÖçÂàó„Çµ„Ç§„Ç∫„ÇíË∂Ö„Åà„Å¶„ÅÑ„Å™„ÅÑ
      if (i - 1 < accidentalArraySize) {
        let accidental = "";
        let lineName = "";
        //ÈÖçÂàóÂÜÖÂÆπ„Å´Âêà„Çè„Åõ„Å¶‚ôØ„Åã‚ô≠„ÅãÊ±∫ÂÆö
        switch (matchChord.accidental.kind[i - 1]) {
          case 0: //Á¨¶Âè∑„Å™„Åó
            break;
          case 1: //‚ôØ
            accidental = "‚ôØ";
            lineName =
              "sharp " +
              cssClassChordName +
              "-" +
              chordClef +
              "-" +
              String(i) +
              "-s";
            noteArray.push(<div className={lineName}>{accidental}</div>);
            //ÊúÄÂàù„ÅÆË¶ÅÁ¥†‰ª•Â§ñ„ÉàÈü≥„Å∏Èü≥„ÅÆÈü≥„ÇÑË®òÂè∑„ÅÆÁõ∏ÂØæ‰ΩçÁΩÆ„ÅØÂü∫Êú¨ÁöÑ„Å´Âêå„Åò
            if (i === 1) chordClef = "g";
            break;
          case 2: //‚ô≠
            accidental = "‚ô≠";
            lineName =
              "flat " +
              cssClassChordName +
              "-" +
              chordClef +
              "-" +
              String(i) +
              "-f";
            noteArray.push(<div className={lineName}>{accidental}</div>);
            //ÊúÄÂàù„ÅÆË¶ÅÁ¥†‰ª•Â§ñ„ÉàÈü≥„Å∏Èü≥„ÅÆÈü≥„ÇÑË®òÂè∑„ÅÆÁõ∏ÂØæ‰ΩçÁΩÆ„ÅØÂü∫Êú¨ÁöÑ„Å´Âêå„Åò
            if (i === 1) chordClef = "g";
            break;
        }
      }
    }

    //Èü≥Á¨¶
    let cName = "note " + cssClassChordName + "-" + chordClef + "-" + String(i);
    noteArray.push(<div className={cName}>ùÖù</div>);
    //ÊúÄÂàù„ÅÆË¶ÅÁ¥†‰ª•Â§ñ„ÉàÈü≥„Å∏Èü≥„ÅÆÈü≥„ÇÑË®òÂè∑„ÅÆÁõ∏ÂØæ‰ΩçÁΩÆ„ÅØÂü∫Êú¨ÁöÑ„Å´Âêå„Åò
    if (i === 1) chordClef = "g";
  }

  return divJoin(noteArray);
};

//div„ÇíÁµêÂêà„Åó„Å¶Ëøî„Åô
const divJoin = (divArray: object[]) => {
  let joinedDiv;
  switch (divArray.length) {
    case 3:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
        </div>
      );
      break;
    case 4:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
        </div>
      );
      break;
    case 5:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
        </div>
      );
      break;
    case 6:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
          {divArray[5]}
        </div>
      );
      break;
    case 7:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
          {divArray[5]}
          {divArray[6]}
        </div>
      );
      break;
    case 8:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
          {divArray[5]}
          {divArray[6]}
          {divArray[7]}
        </div>
      );
      break;
    case 9:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
          {divArray[5]}
          {divArray[6]}
          {divArray[7]}
          {divArray[8]}
        </div>
      );
      break;
    case 10:
      joinedDiv = (
        <div>
          {divArray[0]}
          {divArray[1]}
          {divArray[2]}
          {divArray[3]}
          {divArray[4]}
          {divArray[5]}
          {divArray[6]}
          {divArray[7]}
          {divArray[8]}
          {divArray[9]}
        </div>
      );
      break;
    default:
      joinedDiv = <div>default</div>;
      break;
  }
  return <div className="chord">{joinedDiv}</div>;
};
